import { FileCollection, ServerFile } from "@rp/common/utils"
import { InfiniteData, QueryKey } from "@tanstack/react-query"

const getFileFromResponseForGetMetadata = (
  res: Array<[QueryKey, InfiniteData<FileCollection, unknown> | undefined]>,
  id: string | undefined,
): ServerFile | undefined => {
  const files = res
    .map(([_queryKey, data]) => {
      return data?.pages
        .flat()
        .map(file => file.results)
        .flat()
        .find(file => file?.file_id === id)
    })
    .flat()

  const returnFile = files.find(file => file?.file_id === id)

  return returnFile
}

export { getFileFromResponseForGetMetadata }
